body {
  margin: 0;
  font-family: 'Josefin Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.navbar{
  background-color: #fff !important;
  font-family: 'Josefin Sans', sans-serif;

 
 
}
.navbar-brand{
  font-size: 1.9rem !important;
  color: #565387 !important;
}
.navbar-nav{
margin-left: 45rem;
}
.navbar a{
  font-size: 1.1rem ;
  text-transform: capitalize;
  
}
.menu-active{
  font-weight: bold;
  border-bottom: 1px solid  #565387 ;
}
.navbar a:hover {
  color: #3498db !important;
}

@media (max-width: 991px ){
  .navbar-brand{
    font-size: 1.5rem !important;
  }
  .menu-active{
    font-weight: bold;
    border-bottom: none;
  
}
}

#header h6{
  font-size: 14px;
}
  
#header  {
  width: 100%;
  height: 85vh;

 
  font-family: 'Josefin Sans', sans-serif;
  overflow: hidden;
}
#service{
  
  font-family: 'Josefin Sans', sans-serif;
}
#contact{
  
  font-family: 'Josefin Sans', sans-serif;

}
.card{
  
  font-family: 'Josefin Sans', sans-serif;

}
#header h1{
  margin: 0 0 10px 0;
  font-size: 40px;
  font-weight: 700;
  color: black;
}
#header h5{
  color: #484848;
  font-size: 20px;
}

#header .btn-get-started{
  font-weight: 500;
  font-size: 14px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 8px 22px;
  border-radius: 50px;
  transition: 0.6s;
  color: #3498db ;
  border: 2px solid #3498db ;
  text-decoration: none;
}
#header .brand-name{
  color: #3498db;
  font-size: 42px;
  font-weight: bold;
}

#header .btn-get-started:hover{
  background-color: #3498db;
  color: #fff;
  border: solid 2px #fff;
}


.header-img{
  text-align: right;

  margin-bottom: 100px;
 
}
.about-img{
 margin-left: 50px;

  margin-bottom: 10px;
 
}

.foot{
  
  position: fixed;

}

.foot p{
  font-size: 12px;
}

#header .animated {
  animation: up-down 2s ease-in-out infinite alternate-reverse both;
}
@-webkit-keyframes up-down{
  0%{
    transform: translateY(10px);
  }
  100%{
    transform: translateY(-10px);
  }
}
@keyframes up-down {
  0%{
    transform: translateY(10px);
  }
  100%{
    transform: translateY(-10px);
  }
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
